@import url(https://fonts.googleapis.com/css2?family=Sigmar+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  min-height:100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menuContainer {
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.InnerMenu {

}

.MenuHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.HeaderText {
    font-size: 17px;
    color: white;
    transform: translateY(10px);
    text-align: center;
}

.InnerInnerMenu {
    padding: 10px;
    border-radius: 10px;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(7, 1fr);
}

.DateCardOuter {
    background-color: white;
    display: flex;
    justify-content: center;
    margin: 1vw;
    padding: 1vw;
    border-radius: 1vw;
}

.DateCardInner {
    background-color: white;
    display: flex;
    align-content: space-around;
    justify-content: space-around;
    flex-direction: column;
}

.DateCardInnerInner {
    display: flex;
    align-content: center;
    justify-content: center;
}

.DateItem {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    font-weight: bold;
    font-size: 150%;
}

.PromptItem {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
}

@media only screen and (max-width: 500px) {
    .DateItem {
        font-size: 10px;
    }
    .PromptItem {
        fonr-size: 10px;
    }
}

.BackButton {
    position: fixed;
    top: 10px;
    left: 10px;
}
.Centerer {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.RadioContainer {
    position: absolute;
    margin: 50px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.RadioButton {
    z-index: 1000;
    background-color: Black;
    padding: 10px;
    border-radius: 10px;
    color: white;
    font-size: 32px;
}

.AnItem {
    background-color: darkgrey;
    x-index: -1;
}

.Visualiser {
    position: absolute;
}
.bulky {
    font-family: 'Sigmar One', cursive;
    font-size: 32vw;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}


.FishTank {
    width: 100vw;
    height: 100vh;
}


.Smoke {
    width: 100vw;
    height: 100vh;
}

